import noUiSlider from 'noUiSlider';
document.addEventListener('DOMContentLoaded', function() {

// filters top
  function priceFilter() {
    var priceSelect = document.getElementById('min-price');
    for ( var i = 0; i <= 1000; i++ ) {
      var option = document.createElement('option');
      option.text = i;
      option.value = i;
      priceSelect.appendChild(option);
    }
    var priceSlider = document.getElementById('priceSlider');
    noUiSlider.create(priceSlider, {
      start: [ 1800000, 3000000 ],
      connect: true,
      step: 1000,
      range: {
        'min': 0,
        '10%': 1800000,
        '65%': 3000000,
        'max': 10000000
      },
      animate: true,
      animationDuration: 300
    });

    var inputNumber = document.getElementById('input-price');
    priceSlider.noUiSlider.on('update', function( values, handle ) {
      var value = values[handle];
      if ( handle ) {
        inputNumber.value = Math.round(value);
      } else {
        priceSelect.value = Math.round(value);
      }
    });
    priceSelect.addEventListener('change', function() {
      priceSlider.noUiSlider.set([this.value, null]);
    });

    inputNumber.addEventListener('change', function() {
      priceSlider.noUiSlider.set([null, this.value]);
    });
  }

  function areaFilter() {
    var areaSelect = document.getElementById('min-area');
    // Append the option elements
    for ( var i = 0; i <= 500; i++ ) {

      var option = document.createElement('option');
      option.text = i;
      option.value = i;

      areaSelect.appendChild(option);
    }
    var areaSlider = document.getElementById('areaSlider');
    noUiSlider.create(areaSlider, {
      start: [ 35, 42 ],
      connect: true,
      step: 1,
      range: {
        'min': 0,
        '10%': 35,
        '45%': 42,
        'max': 500
      },
      animate: true,
      animationDuration: 300
    });

    var inputNumber = document.getElementById('input-area');
    areaSlider.noUiSlider.on('update', function( values, handle ) {
      var value = values[handle];
      if ( handle ) {
        inputNumber.value = Math.round(value);
      } else {
        areaSelect.value = Math.round(value);
      }
    });
    areaSelect.addEventListener('change', function() {
      areaSlider.noUiSlider.set([this.value, null]);
    });

    inputNumber.addEventListener('change', function() {
      areaSlider.noUiSlider.set([null, this.value]);
    });
  }

  function levelFilter() {
    var levelSelect = document.getElementById('min-level');
    // Append the option elements
    for ( var i = 0; i <= 30; i++ ) {

      var option = document.createElement('option');
      option.text = i;
      option.value = i;

      levelSelect.appendChild(option);
    }
    var levelSlider = document.getElementById('levelSlider');
    noUiSlider.create(levelSlider, {
      start: [ 3, 12 ],
      connect: true,
      step: 1,
      range: {
        'min': 0,
        '10%': 3,
        '52%': 12,
        'max': 30
      },
      animate: true,
      animationDuration: 300
    });

    var inputNumber = document.getElementById('input-level');
    levelSlider.noUiSlider.on('update', function( values, handle ) {
      var value = values[handle];
      if ( handle ) {
        inputNumber.value = Math.round(value);
      } else {
        levelSelect.value = Math.round(value);
      }
    });
    levelSelect.addEventListener('change', function() {
      levelSlider.noUiSlider.set([this.value, null]);
    });

    inputNumber.addEventListener('change', function() {
      levelSlider.noUiSlider.set([null, this.value]);
    });
  }


  // zhka
  function costFilter() {
    var costInput = document.getElementById('input-cost');
    for ( var i = 0; i <= 1000; i++ ) {
      var option = document.createElement('option');
      option.text = i;
      option.value = i;
      costInput.appendChild(option);
    }
    var costSlider = document.getElementById('costSlider');
    noUiSlider.create(costSlider, {
      start: [3800000],
      connect: [true, false],
      step: 1000,
      range: {
        'min': 0,
        '52%': 3800000,
        'max': 10000000
      },
      animate: true,
      animationDuration: 300
    });
    costSlider.noUiSlider.on('update', function( values, handle ) {
      var value = values[handle];
      if ( handle ) {
        costInput.value = Math.round(value);
      } else {
        costInput.value = Math.round(value);
      }
    });
    costInput.addEventListener('change', function() {
      costSlider.noUiSlider.set([this.value, null]);
    });
  }

  function downPayment() {
    var costInput = document.getElementById('input-downPayment');
    for ( var i = 0; i <= 1000; i++ ) {
      var option = document.createElement('option');
      option.text = i;
      option.value = i;
      costInput.appendChild(option);
    }
    var costSlider = document.getElementById('downPaymentSlider');
    noUiSlider.create(costSlider, {
      start: [420000],
      connect: [true, false],
      step: 1000,
      range: {
        'min': 0,
        '27%': 420000,
        'max': 10000000
      },
      animate: true,
      animationDuration: 300
    });
    costSlider.noUiSlider.on('update', function( values, handle ) {
      var value = values[handle];
      if ( handle ) {
        costInput.value = Math.round(value);
      } else {
        costInput.value = Math.round(value);
      }
    });
    costInput.addEventListener('change', function() {
      costSlider.noUiSlider.set([this.value, null]);
    });
  }
  function creditTerm() {
    var costInput = document.getElementById('input-creditTerm');
    for ( var i = 0; i <= 1000; i++ ) {
      var option = document.createElement('option');
      option.text = i;
      option.value = i;
      costInput.appendChild(option);
    }
    var costSlider = document.getElementById('creditTermSlider');
    noUiSlider.create(costSlider, {
      start: [15],
      connect: [true, false],
      step: 1,
      range: {
        'min': 0,
        '60%': 15,
        'max': 40
      },
      animate: true,
      animationDuration: 300
    });
    costSlider.noUiSlider.on('update', function( values, handle ) {
      var value = values[handle];
      if ( handle ) {
        costInput.value = Math.round(value);
      } else {
        costInput.value = Math.round(value);
      }
    });
    costInput.addEventListener('change', function() {
      costSlider.noUiSlider.set([this.value, null]);
    });
  }

  if (document.getElementsByClassName('all-filters').length > 0) {
    priceFilter();
    areaFilter();
    levelFilter();
  }

  if (document.getElementsByClassName('th-filters').length > 0) {
    priceFilter();
    areaFilter();
  }

  if (document.getElementsByClassName('full-section-card').length > 0) {
    creditTerm();
    downPayment();
    costFilter();
  }
  
});

